<template>
  <div>
    Titulo: {{titulo}}
  <button @click="comprobarContador">
      You clicked me {{ count }} times.
    </button>


  </div>
</template>

<script>
export default {
  name: 'micomponente',
  props: {
    titulo: String,
    url: String
  },
  emits: ['llegada'],
   data() {
    return {
      count: 0
    }
   },
   methods:{
     comprobarContador(){
       this.count++
       if (this.count==10)
       {
          console.log("CONTADOR VALE 10");
          this.$emit('llegada', this.titulo);
       }
     }
   }
}
</script>

